.display1{
  font-size: 8.125em;
  line-height: 110%;
  letter-spacing: -0.07em;
}

.display2{
  font-size: 3.75em;
  line-height: 110%;
  letter-spacing: -0.05em;
}

.text30{
  font-size: 1.875em;
  line-height: 200%;
  letter-spacing: -0.05em;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
  letter-spacing: -0.05em;
}

.text16{
  font-size: 1em;
  line-height: 118.75%;
}

.accent-text{
  color: $primary;
}

._uppercase-text{
  text-transform: uppercase;
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 3.75em;
  }

  .display2{
    font-size: 2.5em;
  }

  .text30{
    font-size: 1em;
    line-height: 100%;
  }

  .text20{
    font-size: 1em;
    line-height: 118.75%;
  }
}


@media screen and (max-width: 576px){
  .display1,
  .display2{
    font-size: 2.5em;
  }
}
