.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 1.375em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 10em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  font-family: $font2;
  &__link{
    margin-left: 1.875em;
    color: #fff;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .8125em 0;
    &__logo{
      position: relative;
      z-index: 11;
      width: 7.8125em;
    }
    &._open{
      .burger{
        &:before{
          transform: translate(0,.25em) rotate(-45deg);
        }
        &:after{
          transform: translate(0,-.25em) rotate(45deg);
        }
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .nav{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background: $black;
    padding: 5em 1.25em 2em;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 0 0 2em;
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    height: .75em;
    width: 1.6875em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:before,
    &:after{
      content: '';
      display: block;
      height: .25em;
      width: 100%;
      background-color: #fff;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 5.4375em;
    }
  }
}
