@import "../mixins";
.what-group{
  @include grid100gap(.625em);
  padding-top: 1.5em;
  &__title{
    color: #24392A;
  }
  &__desc{
    max-width: 46em;
    margin: 0 auto;
    color: #484848;
  }
  &._active{
    .what-group__title{
      color: $primary;
    }
  }
}

@media screen and (max-width: 992px){
  .what-group{
    .text20{
      font-size: 1.25em;
      line-height: 125%;
      @media screen and (min-width: 577px){
        font-size: 1.125em;
        line-height: 122%;
      }
    }
  }
}
