@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-outline{
    height: 6.25em;
    border: 2px solid $black;
    border-radius: 1.25em;
    color: $black;
    span{
        letter-spacing: -0.05em;
        font-size: 1.875em;
    }
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

@media screen and (max-width: 992px){
    .m-btn-outline{
        height: 4.375em;
        span{
            font-size: 1.875em;
        }
    }
}

@media screen and (max-width: 576px){
    .m-btn-outline{
        height: 3.75em;
        span{
            font-size: 1.25em;
        }
    }
}
