.footer{
  padding: 3.75em 0 1.875em;
  background-color: $black;
  border-radius: 1.25em 1.25em 0 0;
  color: #FFFFFF;
  &__top{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.1875em));
    gap: 3.75em 4.375em;
  }
  &__logo{
    display: block;
    width: 15.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 3.625em;
    max-width: 30.3125em;
    font-family: $font2 ;
    span{
      font-size: 2em;
      line-height: 121.875%;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.75em;
  }
  &__subtitle{
    font-size: 1.5em;
    line-height: 120.8%;
    font-weight: 500;
  }
  &__desc2{
    margin-top: 1.25em;
    a{
      text-decoration: underline;
    }
  }
  &__c{
    margin-top: 3.75em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 1.25em;
    &__logo{
      width: 8.625em;
    }
    &__desc{
      margin-top: 1.875em;
      max-width: 16.75em;
      span{
        font-size: 1.25em;
      }
    }
    &__items{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__subtitle{
      font-size: 1.25em;
      line-height: 125%;
    }
    &__desc2{
      font-size: .875em;
      line-height: 121.4%;
    }
    &__c{
      margin-top: 1.875em;
    }
  }
}

@media screen and (max-width: 576px){

  .footer{
    padding: 1.875em 0 1.25em;
    &__top{
      grid-template-columns: 100%;
    }
    &__logo{
      width: 10.25em;
    }
    &__desc{
      margin-top: 1.875em;
      span{
        font-size: 1.25em;
        line-height: 100%;
      }
    }
    &__items{
      gap: 1.875em;
    }
    &__subtitle{
      font-size: 1.875em;
      line-height: 120%;
    }
    &__desc2{
      margin-top: .625em;
      font-size: 1em;
    }
    &__c{
      text-align: center;
      margin-top: 3.75em;
    }
  }
}
