@import "../mixins";
.main{
  border-radius: 1.25em;
  background: radial-gradient(46.84% 105.99% at 5.66% 98.5%, #6FFE95 0%, rgba(111, 254, 149, 0) 100%), radial-gradient(55.25% 55.25% at 50.97% 44.75%, #3F69FD 0%, rgba(63, 85, 253, 0) 100%), radial-gradient(37% 45.19% at 40.1% 42.56%, #6FFE95 0%, rgba(111, 254, 149, 0) 100%), #020202;
  padding: 8.875em 0 10.6875em;
  &__inner{
    text-align: center;
  }
  &__title{
    color: #fff;
    text-transform: uppercase;
  }
  &__desc{
    font-family: $font2;
  }
}

.about{
  padding: 13.125em 0 19.25em;
  border-radius: 1.25em;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  &__inner{
    position: relative;
    z-index: 2;
    text-align: center;
    max-width: 63.75em;
    margin: 0 auto;
  }
  &__desc{
    margin-bottom: 1.875em;
  }
}

.about1{
  color: #fff;
  background-image: url(../img/about.webp);
}

.who{
  padding: 10em 0;
}

.live{
  padding: 18.75em 0 19.25em;
  color: #fff;
  background-image: url(../img/live.webp);
}

.contacts{
  padding: 10em 0;
  &__info{
    @include grid100gap(.625em);
    text-align: center;
  }
  &__form{
    margin: 5.625em auto 0;
    width: 63.75em;
    max-width: 100%;
    @include grid100gap(.625em);
  }
  &__btn{
    margin-top: 2.5em;
  }
}

.what{
  background-color: $black;
  border-radius: 1.25em;
  color: #fff;
  text-align: center;
  overflow: hidden;
  padding: 10em 0;
  &__inner{
    max-width: 64.125em;
    margin: 0 auto;
  }
  &__items{
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 6.625em 0;
    &__inner{
      max-width: 41.25em;
      margin: 0 auto;
    }
    @media screen and (min-width: 577px){
      .display1{
        font-size: 5em;
      }
    }
    &__desc{
      font-size: 1.875em;
      margin-top: .67em;
    }
  }

  .about{
    padding: 9.375em 0 11.25em;
    &__inner{
      max-width: 41.25em;
      margin: 0 auto;
    }
  }

  .who{
    padding: 7.5em 0;
    .about__inner{
      max-width: 35em;
      margin: 0 auto;
    }
    @media screen and (min-width: 577px){
      .display1{
        font-size: 5em;
      }
    }
  }

  .live{
    padding: 10.6875em 0;
    .container{
      padding: 0 .25em;
    }
  }

  .contacts{
    padding: 7.5em 0 8.75em;

    &__form{
      margin-top: 3.25em;
      gap: 1.25em;
      width: 35.1875em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }

  .what{
    padding: 7.5em 0;
    &__inner{
      max-width: 35.4375em;
    }
  }
}



@media screen and (max-width: 576px){
  .main{
    padding: 4.1875em 0 2.5em;
    &__desc{
      font-size: 1.25em;
      margin: 0;
    }
  }

  .about{
    padding: 13.75em 0 3.75em;
  }

  .about1{
    background-image: url(../img/about2.webp);
  }

  .who{
    padding: 3.75em 0;
    .about__inner{
      padding: 0 1.25em;
    }
  }

  .what{
    padding: 3.75em 0;
  }

  .live{
    padding: 19.25em 0 3.75em;
    background-image: url(../img/live2.webp);
  }

  .contacts{
    padding: 3.75em 0 6.25em;
    &__info{
      max-width: 16.25em;
      margin: 0 auto;
    }
    &__form{
      margin-top: 3.75em;
      gap: 1.875em;
    }
    &__btn{
      margin: 0;
    }
  }
}
