.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: .067em solid $gray;
    padding: 0;
    height: 1.27em;
    width: 100%;
    font-size: 3.75em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus{
      border-color: $primary;
    }
  }
}

@media screen and (max-width: 992px){
  .fg{
    input{
      font-size: 2.5em;
      border-width: .1em;
      padding-bottom: .1em;
    }
  }
}
